//   Gradio    API
import { Client } from "https://cdn.jsdelivr.net/npm/@gradio/client/dist/index.min.js";

/**
 *       
 *  API     Hugging Face
 */
export class EnglishChatBotAPI {
    constructor() {
        //   API   
        this.API_URL = "YoussefA7med/ChatBot_English_Helper";
        this.client = null;
        
        //    
        this.chatHistory = [];
        this.transcript = "";
        this.corrections = "";
        
        //      
        this.initialize();
    }

    /**
     *     API
     *     
     */
    async initialize() {
        try {
            console.log("    ...");
            
            //   API
            this.client = await Client.connect(this.API_URL);
            
            console.log("   !");
            
            //      
            await this.initializeChat();
            
        } catch (error) {
            console.error("   :", error);
            throw new Error("    ");
        }
        finally{
            
        }
    }

    /**
     *       
     *  endpoint  
     */
    async initializeChat() {
        try {
            console.log("  ...");
            
            //  API  
            const result = await this.client.predict("/initialize_chat", {});
            
            //    
            this.chatHistory = result.data[0];
            const welcomeAudio = result.data[1];
            this.transcript = result.data[2];
            this.corrections = result.data[3];
            
            console.log("    !");
            console.log("  :", this.transcript);
            
            //     
            if (welcomeAudio) {
                console.log("    ");
                this.playAudio(welcomeAudio);
            }
            
            return {
                history: this.chatHistory,
                transcript: this.transcript,
                corrections: this.corrections,
                audio: welcomeAudio
            };
            
        } catch (error) {
            console.error("    :", error);
            throw error;
        }
    }

    /**
     *        
     * @param {File|Blob} audioFile -    
     * @returns {Object} -      
     */
    async sendAudio(audioFile) {
        try {
            console.log("   ...");
            
            //    
            if (!this.client) {
                throw new Error("    ");
            }
            
            //       
            const result = await this.client.predict("/process_audio", {
                audio: audioFile,
                history: this.chatHistory,
                transcript: this.transcript,
                corrections: this.corrections
            });
            
            //  
            this.chatHistory = result.data[0];
            const responseAudio = result.data[1];
            this.transcript = result.data[2];
            this.corrections = result.data[3];
            
            console.log("    !");
            console.log("  :", this.transcript);
            console.log(" :", this.corrections);
            
            //    
            if (responseAudio) {
                console.log("    ...");
                this.playAudio(responseAudio);
            }
            
            return {
                history: this.chatHistory,
                transcript: this.transcript,
                corrections: this.corrections,
                audio: responseAudio
            };
            
        } catch (error) {
            console.error("    :", error);
            throw error;
        }
    }

    /**
     *      
     *  endpoint  
     */
    async clearChat() {
        try {
            console.log("  ...");
            
            //  API  
            const result = await this.client.predict("/clear_chat", {});
            
            //   
            this.chatHistory = result.data[0];
            const newWelcomeAudio = result.data[1];
            this.transcript = result.data[2];
            this.corrections = result.data[3];
            
            console.log("     !");
            
            //    
            if (newWelcomeAudio) {
                this.playAudio(newWelcomeAudio);
            }
            
            return {
                history: this.chatHistory,
                transcript: this.transcript,
                corrections: this.corrections,
                audio: newWelcomeAudio
            };
            
        } catch (error) {
            console.error("    :", error);
            throw error;
        }
    }

    /**
     *    
     * @param {string} audioUrl -   
     */
    playAudio(audioUrl) {
        try {
            //   audio 
            const audio = new Audio(audioUrl.url);
            // audio.setAttribute("src",audioUrl)
            // console.log(audioUrl);
            
            
            //  
            audio.play().catch(error => {
                console.error("    :", error);
            });
            
            console.log("   ");
            
        } catch (error) {
            console.error("    :", error);
        }
    }

    /**
     *     
     * @param {number} duration -    (: 5 )
     * @returns {Promise<Blob>} -   
     */
    async recordAudio(duration = 5) {
        try {
            console.log("   ...");
            
            //    
            const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
            
            //   
            const mediaRecorder = new MediaRecorder(stream);
            const audioChunks = [];
            
            //   
            mediaRecorder.ondataavailable = (event) => {
                audioChunks.push(event.data);
            };
            
            //  promise    
            const recordingPromise = new Promise((resolve) => {
                mediaRecorder.onstop = () => {
                    //     
                    const audioBlob = new Blob(audioChunks, { type: 'audio/wav' });
                    resolve(audioBlob);
                };
            });
            
            //  
            mediaRecorder.start();
            
            //     
            setTimeout(() => {
                mediaRecorder.stop();
                stream.getTracks().forEach(track => track.stop());
                console.log("   ");
            }, duration * 1000);
            
            console.log(`    ${duration} ...`);
            
            return await recordingPromise;
            
        } catch (error) {
            console.error("    :", error);
            throw error;
        }
    }

    /**
     *      
     * @returns {Array} -  
     */
    getChatHistory() {
        return this.chatHistory;
    }

    /**
     *      
     * @returns {string} -  
     */
    getTranscript() {
        return this.transcript;
    }

    /**
     *     
     * @returns {string} -  
     */
    getCorrections() {
        return this.corrections;
    }
}

// =======================
//   
// =======================

/**
 *      
 */
async function basicExample() {
    try {
        console.log("   ...");
        
        //      
        const chatBot = new EnglishChatBotAPI();
        
        //   
        await new Promise(resolve => setTimeout(resolve, 3000));
        
        //    5 
        const audioFile = await chatBot.recordAudio(5);
        
        //     
        const response = await chatBot.sendAudio(audioFile);
        
        console.log("  :");
        console.log("- :", response.transcript);
        console.log("- :", response.corrections);
        
    } catch (error) {
        console.error("   :", error);
    }
}

/**
 *     HTML
 */
function createChatInterface() {
    //  HTML 
    const interfaceHTML = `
        <div id="chat-container" style="max-width: 800px; margin: 0 auto; padding: 20px;">
            <h2>    </h2>
            
            <div id="chat-messages" style="height: 400px; overflow-y: auto; border: 1px solid #ccc; padding: 10px; margin-bottom: 20px;"></div>
            
            <div style="display: flex; gap: 10px; margin-bottom: 20px;">
                <button id="record-btn" style="flex: 1; padding: 10px; background: #007bff; color: white; border: none; border-radius: 5px;">
                      
                </button>
                <button id="clear-btn" style="flex: 1; padding: 10px; background: #dc3545; color: white; border: none; border-radius: 5px;">
                      
                </button>
            </div>
            
            <div id="corrections" style="background: #f8f9fa; padding: 15px; border-radius: 5px; min-height: 100px;">
                <h4>  </h4>
                <p id="corrections-text">   ...</p>
            </div>
        </div>
    `;
    
    //    
    document.body.innerHTML = interfaceHTML;
    
    //    
    const chatBot = new EnglishChatBotAPI();
    
    //  
    let isRecording = false;
    
    //  
    const messagesDiv = document.getElementById('chat-messages');
    const recordBtn = document.getElementById('record-btn');
    const clearBtn = document.getElementById('clear-btn');
    const correctionsText = document.getElementById('corrections-text');
    
    /**
     *     
     */
    function addMessage(sender, message, isAudio = false) {
        const messageDiv = document.createElement('div');
        messageDiv.style.cssText = `
            margin: 10px 0;
            padding: 10px;
            border-radius: 10px;
            ${sender === '' ? 'background: #007bff; color: white; text-align: right;' : 'background: #f8f9fa; color: black;'}
        `;
        
        messageDiv.innerHTML = `
            <strong>${sender}:</strong> ${message}
            ${isAudio ? '<br> <em>   </em>' : ''}
        `;
        
        messagesDiv.appendChild(messageDiv);
        messagesDiv.scrollTop = messagesDiv.scrollHeight;
    }
    
    /**
     *   
     */
    function updateCorrections(corrections) {
        correctionsText.textContent = corrections || '    ';
    }
    
    //  
    recordBtn.addEventListener('click', async () => {
        if (isRecording) return;
        
        try {
            isRecording = true;
            recordBtn.textContent = '  ...';
            recordBtn.disabled = true;
            
            //  
            const audioFile = await chatBot.recordAudio(5);
            
            //  
            const response = await chatBot.sendAudio(audioFile);
            
            //  
            addMessage('', '   ', true);
            addMessage('', '   ', true);
            updateCorrections(response.corrections);
            
        } catch (error) {
            console.error('  :', error);
            addMessage('', '   ');
        } finally {
            isRecording = false;
            recordBtn.textContent = '  ';
            recordBtn.disabled = false;
        }
    });
    
    clearBtn.addEventListener('click', async () => {
        try {
            await chatBot.clearChat();
            messagesDiv.innerHTML = '';
            updateCorrections('');
            addMessage('', '    ');
        } catch (error) {
            console.error('   :', error);
        }
    });
    
    //  
    setTimeout(() => {
        addMessage('', '!     .      !');
    }, 2000);
}

//   
export default EnglishChatBotAPI;

//   
export { basicExample, createChatInterface };

